%heading{
    font-family: $font-display;
    font-weight: 400;
    text-transform: uppercase;

    
}

.heading-1{
    @extend %heading;
    font-size: 3rem;
    line-height: 1;
    color: $color-grey-light-1;
    &--light{
        color: $color-grey-light-1;
    }

    &--dark{
        color: $color-grey-dark-1;
       
    }
}

.heading-2 {
    @extend %heading;
    font-size: 3rem;
    font-style: italic;
    line-height: 1;

    &--light{
        color: $color-grey-light-1;
    }

    &--dark{
        color: $color-grey-dark-1;
       
    }
    

}
.heading-3{
    @extend %heading;
    font-size: 1.6rem;
    color: $color-primary;
    text-transform: uppercase;


    &--light{
        color: $color-grey-light-1;
    }

    &--dark{
        color: $color-grey-dark-1;
       
    }

}

.heading-4{

    @extend %heading;
    
    &--light{
        color: $color-grey-light-1;
    }

    &--dark{
        color: $color-grey-dark-1;
       
    }
    font-size: 1.9rem;
}

.btn{
    background-color: $color-primary;
    color: white;
    border: none;
    border-radius: 0;
    text-transform: uppercase;
    font-size: 1.5rem;
    padding: 1.8rem 3rem;
    cursor: pointer;
    transition: all 0.3s;

    &:hover{
        background-color: $color-primary-dark;
    }
}

// helper classes

.mb-sm{ margin-bottom: 2rem; }
.mb-md{ margin-bottom: 3rem; }
.mb-lg{ margin-bottom: 4rem; }
.mb-hg{ margin-bottom: 8rem; }