.gallery{
    grid-column: full-start/full-end;
    background-color: $color-grey-light-1;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(7, 5vw);
    grid-auto-flow: dense;
    justify-items: stretch;
    align-items: stretch;
    grid-gap: 1rem;
    padding: 2rem;

    &__item{

        &--1{
           grid-column: 1/span 2;
           grid-row: 1/ span 2;
        }
        &--2{
          grid-column: 3/ span 3;
          grid-row: 1/ span 3;
        }
        &--3{
            grid-column: 6/span 1;
            grid-row: 1/3;
            
        }
        &--4{
            grid-column:7/-1 ;
            grid-row: 1/3;
            
        }
        &--5{
            grid-column: 1/span 2;
            grid-row: 3/ span 3;
            
        }
        &--6{
            grid-column: 3/ span 2;
            grid-row: 4/ span 2;
            
        }
        &--7{
            grid-row: 4/5;
            grid-column: 5/6;
            
        }
        &--8{
            grid-column: 6/span 2;
            grid-row: 3/ span 2;
            
        }
        &--9{
            grid-column: 8/span 1;
            grid-row: 3/ span 3;
        }
        &--10{
            grid-column: 1/span 1;
            grid-row: 6/ span 3;
        }
        &--11{
            grid-column: 2/span 2;
            grid-row: 6/ span 2;
            
        }
        &--12{
            grid-column: 4/span 1;
            grid-row: 6/ span 2;
            
        }
        &--13{
            grid-column: 5/span 3;
            grid-row: 5/ span 3;
            
        }
        &--14{
            grid-column: 8/span 1;
            grid-row: 6/ span 2;
            
        }
    }


    &__img{
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}




