// COLOR VARIABLES
$color-primary: #c69963;
$color-primary-dark: #B28451;

$color-secondary: #101d2c;

$color-grey-light-1: #f9f7f6;
$color-grey-light-2: #aaa;

$color-grey-dark-1: #54483A;
$color-grey-dark-2: #6D5D4B;

// FONT VARIABLES
$font-primary: 'Nunito', sans-serif;
$font-display: 'Josefin Sans', sans-serif;

// breakpoints for mediaqueries:
$bp-large:66em; // approxiamately 1050px
$bp-medium:52em; // about 830px
$bp-small:37.5em; // about 600px


// configurations 
html{
    box-sizing: border-box;
    font-size: 62.5%; // = 10px, because the default is 16px; 1rem = 10px

    @media only screen and (max-width:$bp-large){
        font-size: 50%;
    }
    @media only screen and (max-width:$bp-medium){
       // font-size: 37.5%;
    }
}

body{
    
    font-family: "Josefin+Sans" "Nunito";
    font-weight: 300;
    line-height: 1.6;
    color: $color-grey-dark-2;
}
*::before,
*,
*::after{
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}


